import _ from 'lodash'

export default {
  data() {
    return {
      currPage: 1,
      currPageSize: 10,
      inputSearch: '',
      search: '',
      sort: {
        prop: null,
        order: null
      },
    }
  },

  created() {
    this.onInput = _.debounce(() => {
      this.search = this.inputSearch
      this.fetch()
    }, 300)
  },

  methods: {
    onInput() {},

    onSearch() {
      this.fetch()
    },

    handleSizeChange(pageSize) {
      this.currPageSize = pageSize
      this.fetch()
    },

    onSortChange({ prop, order }) {
      this.sort = {
        prop,
        order: order ? order === 'ascending' ? 'asc' : 'desc' : null
      }
    }
  }
}


